export default {
	data() {
		return {
			labelList: [],  //请求回来的一维数组
			resultList1: [], 
			resultList2: [], //变成二维数组渲染,用于渲染
			selectedListIds: [],
			isShow: false,
			addLabel: ''
		}
	},
	created() {
		// 获取所有标签
		this.getAllList()
	},
	onLoad(options) {
		console.log(options)
		// 上一页传过来的id
		this.selectedListIds = JSON.parse(decodeURIComponent(options.ids)) 
	},
	methods: {
		// 获取所有标签
		getAllList() {
			this.labelList = []
			this.resultList1 = []
			this.resultList2 = []
			this.$http.get({
				url: '/v1/rest/mydata/findAllLabel',
				data: {
					userId: uni.getStorageSync('userInfo').userId
				}
			}).then(resp => {
				console.log(resp)
				if(resp.status == 200) {
					this.labelList = resp.data
					this.sliceArr1()
					this.sliceArr2()
				}
			})
		}, 
		sliceArr1() {
			let n = 7
			let len = this.labelList.length
			let lineNum = len % n === 0 ? len / n : Math.floor((len / n) + 1)
			let result = []
			for (let i = 0; i < lineNum; i++) {
				let newList = this.labelList.slice(i*n ,i*n+n)
				result.push(newList)
				this.resultList1 = result
			}
		},
		sliceArr2 () {
			for(let i = 0; i < this.resultList1.length; i++) {
				let n = 4
				let len = this.resultList1[i].length
				let lineNum = len % n === 0 ? len / n : Math.floor((len / n) + 1)
				let result2 = []
				for (let j = 0; j < lineNum; j++) {
					let newList = this.resultList1[i].slice(j*n ,j*n+n)
					result2.push(newList)
				}
				this.resultList2.push(result2)
			}
			this.resultList2 = this.resultList2.reduce(function (a, b) { return a.concat(b)} )
		},
		showAddBox() {
			this.isShow = true
		},
		onChange(v) {
			this.addLabel = v.detail
		},
		selected(it,ind) {
			this.selectedList = []
			// 选中的标签id集合
			let selectedIdIndex = this.selectedListIds.indexOf(it.value)
			if(selectedIdIndex >= 0) {
				this.selectedListIds.splice(selectedIdIndex, 1)
			}else{
				this.selectedListIds.push(it.value)
			}

			this.selectedList = this.labelList.filter(item => {
				return this.selectedListIds.includes(item.value)
			})  
		},
		toAdd() {
			// 创建并提交后台
			this.$http.get({
				url: '/v1/rest/mydata/createLabel',
				data: {
					userId: uni.getStorageSync('userInfo').userId,
					labelName: this.addLabel
				}
			}).then(resp => {
				console.log(resp)
				if(resp.status == 200) {
					this.getAllList()
					this.addLabel = ''
					this.isShow = false
					uni.showToast({
						title: '创建成功！',
						duration: 1500,
						icon: 'none'
					});
				}
			})
		},
		saveSelectLabels() {
			const labels = {
				labelList: this.selectedList,
				labelIds: this.selectedListIds
			}
			uni.setStorage({
				key: 'selectedLabels',
				data: labels,
				success() {
					uni.navigateBack()
				},
				fail() {
					uni.showToast({
						title: '保存失败！',
						duration: 1500,
						icon: 'none'
					});
				}
			})
		}
	}
}
